<template>
	<v-app id="app">
		<div id="stocks" class="myTable">
			<!--HEAD DE LA PAGINA -->
			<vue-headful
				title="Stocks - Aranda Distribuciones"
			></vue-headful>

			<h2>Listado de Stocks</h2>

			<b-button @click="testFetch" class="mb-4" title="Recargar" variant="light">
				<v-icon dark style="color: black">mdi-cached</v-icon>
				Actualizar
			</b-button>
			<!-- ================ALTA PLANES======================== -->
			<b-button
				class="mb-4 ml-2"
				v-b-modal.modal-alta
				@click="altaStock()"
				title="Nuevo Pago de Stock"
				style="color: white"
			>
				<v-icon dark> mdi-plus </v-icon>
				Nuevo Stock
			</b-button>
			<b-modal no-close-on-backdrop id="modal-alta" hide-footer>
				<template #modal-title>
					<h5 class="modal-title">Alta</h5>
				</template>
				<stocks-alta :updateTable="testFetch" />
			</b-modal>
			<!-- =========================================================== -->
			
			<b-button
				class="mb-4 ml-2"
				variant="danger"
				id="btn_del_full"
				title="Eliminar todos los registros"
				style="color: white"
				:disabled="btn_del_full"
				v-b-modal.modal-eliminarTodo
			>
				<v-icon class="mr-2" style="color: white"> mdi-delete </v-icon>
				Eliminar
			</b-button>

			<!-- ================ELIMINAR VARIOS PLANES======================== -->
			<div>
				<b-modal
					no-close-on-backdrop
					ref="my-modal"
					id="modal-eliminarTodo"
					hide-footer
					title="Eliminar"
					ok-only
				>
					<div class="d-block text-center" v-if="selected.length === rows">
						<h3>¿Está seguro de eliminar todos los registros?</h3>
					</div>
					<div class="d-block text-center" v-else>
						<h3 v-if="selected.length>1">¿Está seguro de eliminar {{ selected.length }} registros?</h3>
						<h3 v-else>¿Está seguro de eliminar un registro?</h3>
					</div>

					<b-button class="mt-2" block @click="hideModal" title="Volver Atras">
						Volver Atras
					</b-button>

					<b-button
						class="mt-3"
						variant="danger"
						block
						title="Eliminar"
						@click="delete_all_Stocks()"
					>
						Eliminar
					</b-button>
				</b-modal>
			</div>

			<!-- ======== Formulario de Busqueda ======== -->
			<b-form-group
				label-for="filter-input"
				label-align-sm="right"
				label-size="sm"
				class="mb-0"
				style="width: 100%; padding-bottom: 1em"
				v-show="rows > 0"
			>
				<b-input-group size="sm">
					<b-form-input
						id="filter-input"
						v-model="filter"
						type="search"
						placeholder="Buscar registros"
					></b-form-input>

					<b-input-group-append>
						<b-button :disabled="!filter" @click="filter = ''"
							>Limpiar</b-button
						>
					</b-input-group-append>
				</b-input-group>
			</b-form-group>
			<!-- ======================================== -->

			<div v-if="rows > 0">
				<div v-if="selected.length > 0">
					<pre>
                        Cantidad de registros: {{ rows }} | Filas seleccionadas: {{
							selected.length
						}}</pre
					>
				</div>
				<div v-else>
					<pre>Cantidad de registros: {{ rows }}</pre>
				</div>
				<b-button
					class="mb-4 ml-2"
					size="sm"
					style="color: white"
					title="Seleccionar Todo"
					@click="seleccionar_todas"
					:disabled="btn_select"
				>
					Seleccionar Todo
				</b-button>
				<b-button
					class="mb-4 ml-2"
					size="sm"
					style="color: white"
					title="Limpiar Seleccion"
					@click="limpiar_seleccion"
					:disabled="btn_limpiar"
				>
					Limpiar Seleccion
				</b-button>
			</div>
			<div v-else>
				<pre>Cantidad de registros: {{ rows }}</pre>
			</div>

			<section class="container">
				<b-table :fields="fields"
						 striped
						 sortable
						 responsive
						 hover
						 :items="tabla_stocks 
								| FechaStock(dateDesde, dateHasta)
								| Producto(filter_producto)"
						 show-empty
						 :sticky-header="false"
						 :no-border-collapse="false"
						 :per-page="perPage"
						 :current-page="currentPage"
						 ref="tablaregistros"
						 id="tablaregistros"
						 selectable
						 select-mode="multi"
						 @row-selected="seleccionar_una"
						 empty-text="No hay registros cargados"
						 empty-filtered-text="No hemos encontrado registros que coincidan con lo que está buscando"
						 :filter="filter"
						 @filtered="onFiltered">


					<template #cell(selected)="{ rowSelected }">
						<template v-if="rowSelected">
							<span aria-hidden="true">&check;</span>
							<span class="sr-only">Selected</span>
						</template>
						<template v-else>
							<span aria-hidden="true">&nbsp;</span>
							<span class="sr-only">Not selected</span>
						</template>
					</template>

					<template slot="cell(id_reparto)" slot-scope="data">
						{{ data.value.split('/')[4]}}
					</template>

					<template slot="cell(cerrado)" slot-scope="data">
						<div v-if="data.value === true">
							<svg xmlns="http://www.w3.org/2000/svg"
								 width="16"
								 height="16"
								 fill="currentColor"
								 class="bi bi-check-circle-fill"
								 viewBox="0 0 16 16"
								 style="color: green">
								<path d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0zm-3.97-3.03a.75.75 0 0 0-1.08.022L7.477 9.417 5.384 7.323a.75.75 0 0 0-1.06 1.06L6.97 11.03a.75.75 0 0 0 1.079-.02l3.992-4.99a.75.75 0 0 0-.01-1.05z" />
							</svg>
							SI
						</div>
						<div v-else>
							<svg xmlns="http://www.w3.org/2000/svg"
								 width="16"
								 height="16"
								 fill="currentColor"
								 class="bi bi-x-circle-fill"
								 viewBox="0 0 16 16"
								 style="color: red">
								<path d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0zM5.354 4.646a.5.5 0 1 0-.708.708L7.293 8l-2.647 2.646a.5.5 0 0 0 .708.708L8 8.707l2.646 2.647a.5.5 0 0 0 .708-.708L8.707 8l2.647-2.646a.5.5 0 0 0-.708-.708L8 7.293 5.354 4.646z" />
							</svg>
							NO
						</div>
					</template>

					<template slot="cell(action)" slot-scope="row">
						<div class="mt-3">
							<b-button-group>
								<b-button variant="info"
										  id="button-1"
										  title="Mostrar Información adicional"
										  :disabled="btn_mostrar"
										  @click="row.toggleDetails">
									{{ row.detailsShowing ? "Ocultar" : "Mostrar" }} Detalles
								</b-button>

								<b-button variant="warning"
										  id="button-2"
										  title="Editar este registro"
										  v-b-modal.modal_editar
										  @click="editarStock(row.item, row.index)"
										  :disabled="btn_editar">
									<v-icon class="mr-2"> mdi-pencil </v-icon>
									Editar
								</b-button>

								<b-button variant="success"
										  id="button-2"
										  title="Cerrar este registro"
										  v-b-modal.modal_cerrar
										  @click="cerrarStock(row.item, row.index)"
										  :disabled="btn_cerrar">
									<v-icon class="mr-2"> mdi-check-bold </v-icon>
									Cerrar
								</b-button>

								<b-button variant="danger"
										  id="button-3"
										  @click="showModalinfo(row.item, row.index)"
										  title="Eliminar este registro"
										  :disabled="btn_eliminar">
									<v-icon class="mr-2"> mdi-delete </v-icon>
									Eliminar
								</b-button>
							</b-button-group>
						</div>
					</template>
					<template #row-details="row">
						<b-card>
							<b-list-group horizontal>
								<b-list-group>
									<b-list-group-item>
										<b>Nombre del Stock:</b>
										{{ row.item.nombre }}
									</b-list-group-item>
									<b-card title="Productos: " v-if="row.item.componentes.length>0">

										<b-list-group horizontal>
											<div v-for="componente in row.item.componentes"
												 :key="componente.id">

												<b-list-group>
													<b-list-group-item>
														<b>Producto:</b>
														{{
productos.find(s=>{
                                                                    return s.id_producto == componente.producto.split('/')[4]
                                                                }).nombre
														}}
													</b-list-group-item>
													<b-list-group-item>
														<p>

															<b>Cantidad: </b>
															{{componente.tipo==2?"$":""}}{{componente.cantidad}}
														</p>
													</b-list-group-item>
												</b-list-group>

											</div>
										</b-list-group>
									</b-card>
									<b-list-group-item v-else>
										Sin Productos
									</b-list-group-item>

								</b-list-group>
								&nbsp;
							</b-list-group>
						</b-card>
					</template>
				</b-table>
				<b-container fluid>
					<b-col class="my-1">
						<b-pagination v-model="currentPage"
									  align="center"
									  pills
									  :total-rows="totalRows"
									  :per-page="perPage"
									  aria-controls="table_stocks">
						</b-pagination>
					</b-col>
				</b-container>

				<b-modal no-close-on-backdrop
						 id="modal_editar"
						 ref="my-modaledit"
						 hide-footer
						 title="Editar"
						 ok-only>
					<stocks-update :stocks="editar" :updateTable="testFetch" />
				</b-modal>
				<!-- ================ELIMINAR UN PLAN======================== -->
				<b-modal no-close-on-backdrop
						 id="modal_eliminar"
						 ref="my-modal"
						 hide-footer
						 title="Eliminar"
						 ok-only>
					<div class="d-block text-center">
						<h3>
							¿Esta seguro de eliminar los datos del stock '{{
								infoEliminar.stock.nombre
							}}'?
						</h3>
					</div>
					<b-button class="mt-2" block @click="hideModal" title="Volver Atras">
						Volver Atras
					</b-button>

					<b-button class="mt-3"
							  variant="danger"
							  block
							  title="Eliminar"
							  @click="deleteStock(infoEliminar.stock.id_stock)">
						Eliminar
					</b-button>
				</b-modal>
				<b-modal no-close-on-backdrop
						 id="modal_cerrar"
						 ref="my-modal1"
						 hide-footer
						 title="Cerrar"
						 ok-only>
					<div class="d-block text-center">
						<h3>
							¿Esta seguro de cerrar el stock '{{
								infoCerrar.stock.nombre
							}}'?
						</h3>
					</div>
					<b-button class="mt-2" block @click="hideModal" title="Volver Atras">
						Volver Atras
					</b-button>

					<b-button class="mt-3"
							  variant="success"
							  block
							  title="Cerrar"
							  @click="closeStock(infoCerrar.stock.id_stock)">
						Cerrar
					</b-button>
				</b-modal>
			</section>
			<aside v-show="rows > 0">
				<div>
					<b-card-group deck>
						<b-card
							bg-variant="primary"
							text-variant="white"
							header="REGISTROS POR PAGINA"
							class="text-center"
						>
							<b-form-group label-for="per-page-select" class="mb-0">
								<b-form-select
									id="per-page-select"
									v-model="perPage"
									:options="pageOptions"
									size="sm"
								></b-form-select>
							</b-form-group>
						</b-card>
					</b-card-group>
				</div>
				<br />
				<div>
					<b-card-group deck>
						<b-card
							style="width: 100%"
							bg-variant="primary"
							text-variant="white"
							header="FILTRAR POR"
							class="text-center"
						>
							<div class="accordion" role="tablist" style="width: 100%">
								<b-card no-body>
									<b-card-header header-tag="header" class="p-1" role="tab">
										<b-button block
												  v-b-toggle.accordion-1
												  variant="info"
												  style="font-size: 0.82em">
											FECHA DE PAGO
										</b-button>
									</b-card-header>

									<b-collapse id="accordion-1"
												visible
												accordion="my-accordion"
												role="tabpanel">
										<b-card-body>
											<b-form-group style="color: black"
														  label="Desde"
														  label-for="fechaDesde"
														  @submit.stop.prevent="handleSubmit">
												<b-form-input id="fechaDesde"
															  v-model="dateDesde"
															  type="date"
															  aria-label="Desde"
															  placeholder="Ingrese una Fecha"
															  invalid-feedback="Complete este campo"
															  required>
												</b-form-input>
												{{ dateDesde }}
											</b-form-group>

											<b-form-group style="color: black"
														  label="Hasta"
														  label-for="fechaHasta"
														  @submit.stop.prevent="handleSubmit">
												<b-form-input id="fechaHasta"
															  v-model="dateHasta"
															  type="date"
															  placeholder="Ingrese una Fecha"
															  required>
												</b-form-input>
												{{ dateHasta }}
											</b-form-group>
											<div v-show="dateDesde != null && dateHasta != null">
												<b-button @click="limpiar_filtro_fecha()"
														  title="Limpiar">
													Limpiar
												</b-button>
											</div>
										</b-card-body>
									</b-collapse>
								</b-card>
								<b-card no-body>
									<b-card-header header-tag="header" class="p-1" role="tab">
										<b-button block
												  v-b-toggle.accordion-2
												  variant="info"
												  style="font-size: 0.82em">
											PRODUCTO
										</b-button>
									</b-card-header>
									<b-collapse id="accordion-2"
												visible
												accordion="my-accordion"
												role="tabpanel">
										<b-card-body>
											<b-form-group id="input-group-4">
												<v-autocomplete id="letra"
																v-model="filter_producto"
																:items="options_producto"
																type="text"
																solo
																filled></v-autocomplete>
												<div v-show="filter_producto != null">
													<b-button @click="filter_producto = null"
															  title="Limpiar">
														Limpiar
													</b-button>
												</div>
											</b-form-group>
										</b-card-body>
									</b-collapse>
								</b-card>
							</div>
						</b-card>
					</b-card-group>
				</div>
			</aside>
		</div>
	</v-app>
</template>

<script>
    let api = new URL("http://93.188.162.174:8081");
	api.pathname = "stocks/";
	//api.port = 8000;
	//api.port = 8081;

    import StocksAlta from "./StocksAlta.vue";
    import StocksUpdate from "./StocksUpdate.vue";
	import axios from "axios";

	import { APIControler } from "@/store/APIControler";
	import swal from "sweetalert";

	export default {
        components: { StocksAlta, StocksUpdate },
		data() {
			return {
				tabla_stocks: [],
                productos:[],
				fields: [
					{ key: "selected", label: ""},
					{ key: "id_stock", label: "ID", sortable: true },
					{ key: "nombre", label: "Nombre", sortable: true },
					{ key: "fechastock", label: "Fecha", sortable: true },
					{ key: "id_reparto", label: "ID Reparto", sortable: true },
                    { key: "cerrado", label: "Stock Cerrado", sortable: true },
					{ key: "action", label: "Acciones", variant: "secondary" },
				],
				modal: false,

				dateDesde: null,
				dateHasta: null,
				options_producto: [],
				filter_producto: null,
				editar: {},
				productoelegido: {},
				filter: null,
				totalRows: 1, //Total de filas
				currentPage: 1, //Pagina actual
				perPage: 10, // Datos en la tabla por pagina
				pageOptions: [10, 20, 40, 100, { value: 10000, text: "Todos" }],
				selected: [],
				cambiototal: {},
				infoEliminar: {
					id: "modal_eliminar",
					stock: -1,
				},
                infoCerrar: {
                    id: "modal_cerrar",
                    stock: -1,
                },
				btn_del_full: true,
				btn_mostrar: false,
				btn_editar: false,
				btn_eliminar: false,
				btn_select: false,
                btn_cerrar: false,
				btn_limpiar: true,
			};
		},
		computed: {
			rows() {
				return (this.totalRows = this.tabla_stocks.length);
			},
			sortOptions() {
				// Funcion para filtrar
				return this.fields
					.filter((f) => f.sortable)
					.map((f) => {
						return { text: f.label, value: f.key };
					});
			},
		},

		methods: {
			
			async testFetch() {
				try {
					const res = await fetch(api);
					const data = await res.json();

					this.tabla_stocks = data.results;

                    this.tabla_stocks.forEach((element) => {
                        element.componentes.forEach((prod) => {
                            let opcionFarm = {};
                            opcionFarm.value = prod.producto;
                            opcionFarm.text = this.getProducto(prod.producto.split("/")[4]).nombre;
                            if (
                                this.options_producto.find((x) => x.value == opcionFarm.value)
                            ) {
                                console.log(opcionFarm, " ya se encuentra en el listado");
                            } else {
                                this.options_producto.push(opcionFarm);
                            }
                        })
                    });

				} catch (error) {
					console.log(error);
				}
			},
			//Funcion para mostrar el modal
			showModal() {
				this.$refs["my-modal"].show();
			},
            showModal1() {
                this.$refs["my-modal1"].show();
            },
			//Funcion para esconder el modal
			hideModal() {
				this.$refs["my-modal"].hide();
			},
            hideModal1() {
                this.$refs["my-modal1"].hide();
            },
			info(id) {
				alert("id: " + id);
			},

			showModalinfo(item, index) {
				this.infoEliminar.stock = item;
				this.showModal();
			},

            cerrarStock(item, index) {
                this.infoCerrar.stock = item;
                this.showModal1();
			},

			//-----Funciones de seleccion
			//Selecciona una a una
			seleccionar_una(items) {
				this.selected = items;
				if (this.selected.length > 0) {
					this.btn_del_full = false;
					this.btn_limpiar = false;
					if (this.selected.length > 1) {
						this.btn_mostrar = true;
						this.btn_editar = true;
						this.btn_eliminar = true;
						this.btn_cerrar = true;
					}
					if (this.selected.length == this.rows) {
						this.btn_select = true;
					} else {
						this.btn_select = false;
					}
				} else {
					this.btn_del_full = true;
					this.btn_mostrar = false;
					this.btn_editar = false;
					this.btn_eliminar = false;
					this.btn_limpiar = true;
				}
			},
			//Selecciona todas
			seleccionar_todas() {
				this.$refs.tablaregistros.selectAllRows();
				this.btn_del_full = false;
				this.btn_mostrar = true;
				this.btn_editar = true;
				this.btn_eliminar = true;

				this.btn_select = true;
				this.btn_limpiar = false;
			},
			//Limpia todas las selecciones
			limpiar_seleccion() {
				this.$refs.tablaregistros.clearSelected();
				this.btn_del_full = true;
				this.btn_mostrar = false;
				this.btn_editar = false;
				this.btn_eliminar = false;

				this.btn_select = false;
				this.btn_limpiar = true;
			},

			//Alta de medicamento
			altaStock() {},

			//Editar medicamento
			editarStock(item, index) {
				this.editar = item;
			},
            
            async getProductos() {
				let serviciosAPI = new APIControler();
				serviciosAPI.apiUrl.pathname = "productos/";
				this.productos= await serviciosAPI.getData()
                // .then(servicios=>{
                //     let s=[]
                //     for (d in servicios){
                //         {

                //         }
                //         s.push
                //     }
                // })


                // let option
				// this.data.forEach((element) => {
				// 	option = {};
				// 	option.value = 
				// 		"http://93.188.162.174:8081/servicios/" + element.id_servicio + "/";
				// 	option.text = element.servicio;
				// 	console.log(option);
				// 	this.op_servicios.push(option);
				// });
			},

            getProducto(id){
                if(this.productos==[]){
                    return getProductos().then(()=>{
                        return this.getProducto(id)
                    })
                }
                else{
                    return this.productos.find(s=>{
                        return s.id_producto == id
                    })
                }
			},
            async actualizarStockProducto(id, stock) {
                let productofinal = this.getProducto(id);
                this.productoelegido.id_producto = productofinal.id_producto
                this.productoelegido.nombre = productofinal.nombre
                this.productoelegido.tipo_producto = productofinal.tipo_producto
                this.productoelegido.letra = productofinal.letra
                this.productoelegido.numero_producto = productofinal.numero_producto
                this.productoelegido.codigo_unico = productofinal.codigo_unico
                this.productoelegido.stocktotal = parseInt(productofinal.stocktotal) + parseInt(stock)
                this.productoelegido.precioref = productofinal.precioref
                console.log(this.productoelegido)
                await axios
                    .put(
                        "http://93.188.162.174:8081/productos/" +
                        this.productoelegido.id_producto +
                        "/",
                        this.productoelegido
                    );
            },
			//Funcion para eliminar el medicamento
			async deleteStock(id) {
				axios
                    .delete("http://93.188.162.174:8081/stocks/" + id + "/")
					.then((datos) => {
						if (this.infoEliminar.stock.cerrado == false)
						{
                            this.infoEliminar.stock.componentes.forEach(element => {
                                console.log("Longitud");
                                this.actualizarStockProducto(element.producto.split('/')[4], element.cantidad);
                            });
						}
						swal("Eliminacion Exitosa", " ", "success");
						console.log(datos);
						this.hideModal();
					})
					.catch((error) => {
						swal("¡ERROR!", "Se ha detectado un problema ", "error");
						console.log(error);
						this.hideModal();
					})
					.finally(() => this.testFetch());
			},
			async closeStock(id) {
				if (this.infoCerrar.stock.cerrado == true) {
					window.alert("Este stock ya fue cerrado");
				} else
				{
                    var baseURL = "http://93.188.162.174:8081"
                    const response = await axios.get(baseURL + "/stocks/" + id + "/cerrarelStock/")

                        .then((datos) => {
                            swal("Operación Exitosa", " ", "success");
                            this.infoCerrar.stock.componentes.forEach(element => {
                                this.actualizarStockProducto(element.producto.split('/')[4], element.cantidad);
                            });
                            this.hideModal();
                            sessionStorage.removeItem("pagos");
                        })
                        .catch((error) => {
                            swal("¡ERROR!", "Se ha detectado un problema ", "error");
                            console.log(error);
                            this.hideModal();
                        })
                        .finally(() => this.testFetch());
				}
			},
            redirect() {
                if (!this.$session.exists()) {
                    window.location.replace('/login');
                }
				if (this.$session.get('tipocuenta') == 'reparto') {
					if (this.$session.get('stockused')) {
						window.location.replace('/stockreparto/?id_sto=' + this.$session.get('stockused').id_stock);
					}
					else
					{
                        window.location.replace('/Home');
					}
                }

            },
			//Funcion para eliminar todos los medicamentos seleccionados
			async delete_all_Stocks() {
				var cantidad = this.selected.length;
				try {
					for (var i = 0; i < cantidad; i++) {
						axios.delete(
							"http://93.188.162.174:8081/stocks/" + this.selected[i].id_stock + "/"
						)
							.then((datos) => {
								swal("Operación Exitosa", " ", "success");
                                this.selected[i].componentes.forEach(element => {
									this.actualizarStockProducto(element.producto.split('/')[4], element.cantidad);
								});
							})
						if (this.selected.length == 0) {
							console.log("Eliminacion Exitosa");
							break;
						}
					}
					this.hideModal();
					swal("Eliminacion Exitosa", " ", "success");
				} catch (error) {
					this.hideModal();
					swal("¡ERROR!", "Se ha detectado un problema ", "error");
					console.log(error);
				} finally {
					this.testFetch();
				}
			},

			limpiar_filtro_fecha() {
				this.dateDesde = null;
				this.dateHasta = null;
			},

			onFiltered(filteredItems) {
				// Trigger pagination to update the number of buttons/pages due to filtering
				this.totalRows = filteredItems.length;
				this.currentPage = 1;
			},

		},

		beforeMount() {
            this.getProductos()
			this.testFetch();
			this.redirect();
		},
	};
</script>

